.app{
    width: 100vw;
    height: 100vh;
    background: #000;
    position: relative;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
}

@media screen and (min-width:600px) and (orientation:landscape){
    .app{
        flex-direction: row;
        
    }
}